<template><section><!--
 * @Author: Fone丶峰
 * @Date: 2019-10-22 13:23:50
 * @LastEditors: Fone丶峰
 * @LastEditTime: 2020-04-24 15:47:11
 * @Description: msg
 * @Email: qinrifeng@163.com
 * @Github: https://github.com/FoneQinrf
 -->
<h3>DatetimePicker 时间选择</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { DatetimePicker } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(DatetimePicker)

</code></pre>
<h4>基本用法</h4>
<p><code v-pre>type</code>预设date，可选date、datetime、time三种模式</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">DatetimePicker</span> /&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">DatetimePicker</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"datetime"</span> /&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">DatetimePicker</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"time"</span> /&gt;</span>

</code></pre>
<h4>v-model使用</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">DatetimePicker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">value</span>: <span class="hljs-string">"2020/4/2"</span>
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>禁用</h4>
<p>通过<code v-pre>disabled</code>属性可禁用<code v-pre>DatetimePicker</code></p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">DatetimePicker</span> <span class="hljs-attr">disabled</span> /&gt;</span>

</code></pre>
<h4>事件</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">DatetimePicker</span> <span class="hljs-attr">@on-confirm</span>=<span class="hljs-string">"confirm"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"time"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    confirm(val) {
      <span class="hljs-keyword">this</span>.$Toast.info(val);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>自定义时间格式化</h4>
<p><code v-pre>formatter</code>属性可自定义格式化的函数</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">DatetimePicker</span> <span class="hljs-attr">:formatter</span>=<span class="hljs-string">"formatter"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    formatter(val) {
      <span class="hljs-keyword">return</span> val.replace(<span class="hljs-regexp">/\//g</span>, <span class="hljs-string">"-"</span>);
    }
  },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h3>API</h3>
<h5>DatetimePicker props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>placeholder</td>
<td>占位内容</td>
<td>String</td>
<td>无</td>
<td>请选择</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>cancel-text</td>
<td>取消文本</td>
<td>String</td>
<td>无</td>
<td>取消</td>
</tr>
<tr>
<td>confirm-text</td>
<td>确认文本</td>
<td>String</td>
<td>无</td>
<td>确认</td>
</tr>
<tr>
<td>min-year</td>
<td>时间最小年份</td>
<td>Number</td>
<td>无</td>
<td>1969</td>
</tr>
<tr>
<td>max-year</td>
<td>时间最大年份</td>
<td>Number</td>
<td>无</td>
<td>2025</td>
</tr>
<tr>
<td>v-model</td>
<td>绑定的值</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>type</td>
<td>时间的类型</td>
<td>String</td>
<td><code v-pre>date</code> <code v-pre>datetime</code> <code v-pre>time</code></td>
<td>date</td>
</tr>
<tr>
<td>height</td>
<td>Picker每一项的高度</td>
<td>Number</td>
<td>无</td>
<td>38</td>
</tr>
<tr>
<td>formatter</td>
<td>格式换函数</td>
<td>Function</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>DatetimePicker Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-cancel</td>
<td>点击取消关闭时间选择器时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-change</td>
<td>滑动选择时间时触发</td>
<td>当前选中的时间</td>
</tr>
<tr>
<td>on-show</td>
<td>弹出时间选择框时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-confirm</td>
<td>点击确认时触发</td>
<td>当前选中的时间</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      value: "2020/4/2"
    };
  }
};
</script>


